const FilteredCategories = () => {
    const path = useLocation();
    const params = useParams();
    const categoryName = params.name;
    const entries = Object.entries(products);
    const values = Object.values(products);

    function filterProducts(path: Location) {
        if (path.pathname.includes("category")) {
            return values.filter((el) => el.category[0] === categoryName || el.category[1] === categoryName || el.category[2] === categoryName);
        }
        return values;
    }

    const getProductId = (name: string) => {
        for (let i = 0; i < entries.length; i++) {
            if (name === entries[i][1].name) {
                return entries[i][0];
            }
        }
        return "not found";
    }

    return (<div>
            <Title>{categoryName ? categoryName : "All Products"}</Title>
            <Container>
                {filterProducts(path).map
                ((el) => <ProductPreview key={el.name}
                                         id={getProductId(el.name)}
                                         name={el.name}
                                         price={el.price}
                                         images={el.img}/>)}
            </Container>
        </div>
    );
}

export default FilteredCategories;